        * {
          margin: 0;
          padding: 0;
          box-sizing: border-box;
        }

        .jms-app {
          font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
          min-height: 100vh;
          background-color: #f5f5f5;
        }

        .screen {
          min-height: 100vh;
          display: flex;
          flex-direction: column;
        }

        /* Authentication Screens */
        .auth-container {
          flex: 1;
          padding: 2rem 1.5rem;
          display: flex;
          flex-direction: column;
          justify-content: center;
          max-width: 400px;
          margin: 0 auto;
          width: 100%;
        }

        .logo-container {
          text-align: center;
          margin-bottom: 2rem;
        }

        

        

        

        .jms-logo {
          width: 200%; /* Set the desired width for your icon */
    height: auto; /* Maintain aspect ratio */
    max-width: 300%; /* Ensure it doesn't overflow its parent */
    max-height: 300%; /* Ensure it doesn't overflow its parent */
    object-fit: contain; /* Scales the image to fit without cropping, maintaining aspect ratio */
    position: relative; /* To be above the ::before if you keep it */
    z-index: 2; 
	left: 65px;
        }

        .auth-title {
          text-align: center;
          font-size: 1.5rem;
          font-weight: 600;
          margin-bottom: 2rem;
          color: #333;
        }

        .auth-form {
          display: flex;
          flex-direction: column;
          gap: 1rem;
        }

        .input-group {
          position: relative;
        }

        .auth-input {
          width: 100%;
          padding: 12px 16px;
          border: 2px solid #e1e5e9;
          border-radius: 8px;
          font-size: 16px;
          transition: border-color 0.2s;
          background: white;
        }

        .auth-input:focus {
          outline: none;
          border-color: #ff6600;
        }

        .auth-button {
          width: 100%;
          padding: 12px;
          background: #ff6600;
          color: white;
          border: none;
          border-radius: 8px;
          font-size: 16px;
          font-weight: 600;
          cursor: pointer;
          transition: background-color 0.2s;
          margin-top: 1rem;
        }

        .auth-button:hover {
          background: #e55a00;
        }

        .signup-button {
          background: #333;
          border-radius: 20px;
          width: 100px;
          align-self: flex-end;
        }

        .signup-button:hover {
          background: #555;
        }

        .auth-link {
          text-align: center;
          margin-top: 1.5rem;
          color: #666;
        }

        .link-button {
          background: none;
          border: none;
          color: #ff6600;
          cursor: pointer;
          text-decoration: underline;
          margin-left: 4px;
        }

        /* Dashboard Styles */
        .dashboard {
          background: white;
        }

        .dashboard.dark-mode {
          background: #1a1a1a;
          color: white;
        }

        .dashboard-header {
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 1rem;
          background: white;
          border-bottom: 1px solid #e1e5e9;
          position: relative;
          z-index: 100;
        }

        .dark-mode .dashboard-header {
          background: #2d2d2d;
          border-bottom-color: #404040;
        }

        .menu-toggle {
          background: none;
          border: none;
          font-size: 1.2rem;
          cursor: pointer;
          padding: 4px;
        }

        .header-welcome {
          
          font-size: 14px;
          
          margin-left: 1rem;
        }

        .courthouse-small {
          width: 100px;
  	  margin-left: -29px;
        }

        .welcome-text {
          color: #666;
	  font-size: 20px;
        }

        .dark-mode .welcome-text {
          color: #ccc;
        }

        .header-actions {
          display: flex;
          align-items: center;
        }

        .timer-icon {
          background: #ff6600;
          color: white;
          width: 32px;
          height: 32px;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 14px;
        }

        .dashboard-content {
          display: flex;
          flex: 1;
          position: relative;
        }

        /* Sidebar */
        .sidebar {
          position: fixed;
          left: -280px;
          top: 0;
          width: 280px;
          height: 100vh;
          background: white;
          border-right: 1px solid #e1e5e9;
          transition: left 0.3s ease;
          z-index: 200;
          display: flex;
          flex-direction: column;
          padding-top: 50px;
        }

        .dark-mode .sidebar {
          background: #2d2d2d;
          border-right-color: #404040;
        }

        .sidebar.open {
          left: 0;
        }

        .sidebar-nav {
          flex: 1;
          padding: 1rem 0;
        }

        .nav-item {
          display: flex;
          align-items: center;
          gap: 12px;
          padding: 12px 1.5rem;
          text-decoration: none;
          color: #333;
          transition: background-color 0.2s;
          border: none;
	  background: white;
        }

        .dark-mode .nav-item {
          color: #ccc;
          background: #2d2d2d;
        }

        .nav-item:hover,
        .nav-item.active {
          background: #f8f9fa;
        }

        .dark-mode .nav-item:hover,
        .dark-mode .nav-item.active {
          background: #404040;
        }

        .nav-icon {
          font-size: 18px;
          color: #ff6600;
          width: 20px;
        }

        .nav-label {
          font-size: 14px;
        }

        .logout-btn {
          margin: 1rem 1.5rem;
          padding: 12px;
          background: none;
          border: 1px solid #e1e5e9;
          border-radius: 8px;
          display: flex;
          align-items: center;
          gap: 8px;
          cursor: pointer;
          color: #333;
          font-size: 14px;
        }

        .dark-mode .logout-btn {
          border-color: #404040;
          color: #ccc;
        }

        .logout-icon {
          color: #ff6600;
	 width: 20px;
        }

        /* Main Content */
        .main-content {
          flex: 1;
          padding: 1rem;
        }

        .content-header {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 1.5rem;
        }

        .content-header h1 {
          font-size: 1.5rem;
          font-weight: 600;
        }

        .hearings-section {
          background: #fff9e6;
          border-radius: 12px;
          padding: 1.5rem;
          min-height: 300px;
        }

        .dark-mode .hearings-section {
          background: #404040;
        }

        .section-title {
          font-size: 1.1rem;
          font-weight: 600;
          margin-bottom: 1rem;
          color: #333;
        }

        .dark-mode .section-title {
          color: white;
        }

        .hearings-content {
          min-height: 200px;
          display: flex;
          align-items: center;
          justify-content: center;
        }

        .placeholder-text {
          color: #666;
          font-style: italic;
        }

        .dark-mode .placeholder-text {
          color: #999;
        }

        /* Bottom Navigation */
        .bottom-nav {
          display: flex;
          justify-content: space-around;
          padding: 12px;
          background: white;
          border-top: 1px solid #e1e5e9;
        }

        .dark-mode .bottom-nav {
          background: #2d2d2d;
          border-top-color: #404040;
        }

        .nav-btn {
          position: relative;
          background: none;
          border: none;
          padding: 8px;
          cursor: pointer;
          border-radius: 8px;
          display: flex;
          align-items: center;
          justify-content: center;
        }

        .nav-btn.active {
          background: #fff3e6;
        }

        .dark-mode .nav-btn.active {
          background: #404040;
        }

        .nav-btn .nav-icon {
          font-size: 20px;
        }

        .notification-badge {
          position: absolute;
          top: -4px;
          right: -4px;
          background: #ff4444;
          color: white;
          font-size: 10px;
          padding: 2px 6px;
          border-radius: 10px;
          min-width: 16px;
          text-align: center;
        }

        /* Sidebar Overlay */
        .sidebar-overlay {
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(0, 0, 0, 0.5);
          z-index: 150;
        }

        /* Responsive Design */
        @media (min-width: 768px) {
          .auth-container {
            padding: 3rem 2rem;
          }
          
          .courthouse-icon {
            width: 100px;
            height: 80px;
          }
          
	
          .sidebar {
            position: relative;
            left: 0;
            width: 280px;
            padding-top: 0;
          }
          
          .main-content {
            padding: 2rem;
          }
          
          .dashboard-content {
            margin-bottom: 0;
          }
          
          .bottom-nav {
            display: none;
          }
          
          .menu-toggle {
            display: none;
          }
          
          .sidebar-overlay {
            display: none;
          }
        }

	@media (max-width: 768px){
	.jms-logo{
	width: 100%; /* Set the desired width for your icon */
    	height: auto; /* Maintain aspect ratio */
    	max-width: 300%; /* Ensure it doesn't overflow its parent */
    	max-height: 300%; /* Ensure it doesn't overflow its parent */
    	object-fit: contain; /* Scales the image to fit without cropping, maintaining aspect ratio */
    	position: relative; /* To be above the ::before if you keep it */
    	z-index: 2; 
	left: 0px;
	}
	}

        @media (min-width: 1024px) {
          .hearings-section {
            padding: 2rem;
          }
          
          .dashboard-header {
            padding: 1.5rem 2rem;
          }
        }